import React, { Component } from 'react';
import logo from './logo.svg';
import styled, { keyframes } from 'styled-components';
import { generator, Gradient } from 'uigradients';


const spin = keyframes`
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
`;

const A = styled.a`
  text-decoration: none;
`;

const Header = styled(Gradient)`
  color: white;
  height: 150px;
  margin-bottom: 25px;
  padding: 20px;
  text-align: center;
`;

const H1 = styled.h1`
  font-family: 'Raleway', sans-serif;
  font-weight: 200;
`;

const H2 = styled.h2`
  font-family: 'Raleway', sans-serif;
  font-size: 26px;
  font-weight: 300;
`;

const H3 = styled.h3`
  font-family: 'Raleway', sans-serif;
  font-size: 20px;
  font-weight: 300;
`;

const H4 = styled.h4`
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  font-weight: 400;
`;

const HR = styled.hr`
  border: none;
  ${generator({name: 'colors_of_sky'})}
  height: 1px;
  margin: 40px 0;
`;

const Logo = styled.img`
  animation: ${spin} infinite 20s linear;
  height: 80px;
`;

const Intro = styled.div`
  font-size: large;
  margin: 0 auto;
  max-width: 100%;
  text-align: left;
  width: 500px;
`;

const Code = styled.code`
  display: block;
  content: '${(props) => props.gradient}';
  ${(props) => props.gradient ? generator({name: props.gradient}) : generator()}
  border-radius: 4px;
  color: white;
  font-size: 14px;
  margin-bottom: 0px;
  overflow: scroll;
  padding: 15px;
  white-space: pre;
`;

function Pre(props) {
  const code = props.children; 
  return (
    <Code {...props}>{code}</Code>
  );
}

class App extends Component {
  render() {
    const component_example = `// Import the component...
import { Gradient } from 'uigradients';

// Now, use it!
class App extends Component {
    // If no gradient name is provided,
    // a random one will be chosen.
    return (
      <Gradient gradient="cherry">
        <h1>This will draw a div with a cherry gradient, like the one you're looking at right now.</h1>
      </Gradient>
    );
}
`;

    const generator_example = `import { generator } from 'uigradients';

generator({gradient: 'intuitive_purple'});

/* The function above returns: 
${generator({name: 'intuitive_purple'})}
*/
`;

    const styled_components_example1 = `import { generator } from 'uigradients';
import styled from 'styled-components';

const Pre = styled.pre\`
  \${generator({gradient: 'electric_violet'})}
  border-radius: 4px;
  display: block;
\`;
`;

    const styled_components_example2 = `<Pre>
  This is what an electric_violet pre looks like!
</Pre>`;


    const styled_components_example3 = `import { Gradient } from 'uigradients';
import styled from 'styled-components';

const Header = styled(Gradient)\`
  min-height: 300px;
  text-align: center;
  width: 100%;
\`;
`;
  
    const styled_components_example4 = `<Header gradient="day_tripper">
  Awesome!
</Header>`;

    const usage_generator = `import { generator } from 'uigradients';`;
    return (
      <div className="App">
        <Header name="mantle">
          <Logo src={logo} className="App-logo" alt="logo" />
          <H1>uiGradients</H1>
        </Header>
        <Intro>
          <H2>Installation</H2>
          <p><Code gradient="sunrise">npm install --save uigradients</Code></p>
          <HR/>

          <H2>Examples</H2>
          <H3>Gradient Component</H3>
          <Pre gradient="cherry">{component_example}</Pre>

          <H3>Gradienet Generator</H3>
          <Pre gradient="facebook_messenger">{generator_example}</Pre>

          <H3><A href="http://styled-components.com" title="Styled Components" target="_blank">&lt;&gt;</A> Styled Components</H3>
          <H4>Use the Generator in a styled-component</H4>
          <Pre gradient="intuitive_purple">{styled_components_example1}</Pre>

          <H4>And render the component</H4>
          <Pre gradient="electric_violet">{styled_components_example2}</Pre>

          <H4>Or, you can override the built in Gradient component</H4>
          <Pre gradient="passion">{styled_components_example3}</Pre>
          <H4>And render the component</H4>
          <Pre gradient="day_tripper">{styled_components_example4}</Pre>
          <HR/>
        </Intro>
      </div>
    );
  }
}

export default App;



