import App from './App';
import React from 'react';
import ReactDOM from 'react-dom';
import { injectGlobal } from 'styled-components';


injectGlobal`
  body {
    font-family: 'Raleway', sans-serif;
    font-weight: 300;
    margin: 0;
    padding: 0;
  }
`;

ReactDOM.render(
  <App />,
  document.getElementById('root')
);



